@import "./layout";

$standard-border-radius: 6px;

.react-grid-Main {
  outline: none !important;
}

.react-grid-Grid {
  border-bottom: none !important;
  border-radius: $standard-border-radius $standard-border-radius 0 0;
}

.sheet-tabs {
  background-color: #f1f3f4;
  border-radius: 0 0 $standard-border-radius $standard-border-radius;
}

.sheet-tab {
  display: inline-block;
  border-color: #d8d8d8;
  align-items: center;
  background-color: #f1f3f4;
  border-bottom: none;
  border-top-color: rgb(232, 234, 237);
  display: inline-flex;
  height: 37px;
  margin-right: -1px;
  // padding: 0 12px 0 16px;
  padding: 0;
	border-bottom: 1px solid #dddddd;
	border-left: 1px solid #dddddd;
  border-right: 1px solid #dddddd;
  border-top-width: 0;
  border-bottom-width: 0;
  span {
    padding: 0 12px 0 16px;
  }
}

.sheet-tab:first-child {
  border-radius: 0px 0 0 $standard-border-radius;
}

.sheet-tab-active {
  background-color: #fff;
  border-top-color: #f1f3f4;
  box-shadow: 0 1px 3px 1px rgba(60,64,67,.15);
}

.sheet-tabs {
  border-right: 1px solid rgb(204, 204, 204);
  border-top: 1px solid rgb(204, 204, 204);
  border-bottom: 1px solid rgb(204, 204, 204);
  outline: none;
}
.react-grid-Row:last-child {
  .react-grid-Cell {
    border-bottom: none;
  }
}
.react-grid-HeaderCell input[type="checkbox"] {
  margin-right: 10px;
}

.absoluteWrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}

.react-grid-Main{
  height: 100%;
}

.react-grid-Container{
  height: 100%;
}

.react-grid-Grid{
  min-height: 100% !important;
}

.react-grid-Canvas{
  height: 100% !important;
}

.is-link.card-footer-item {
  background: none !important;
  border: none;
  cursor: pointer;
  box-sizing: border-box;
  &:hover{
    color: #363636;
  }
  color: #3273DC;
  &:not(:last-child) {
    border-right: 1px solid #ededed;
  }
}

.buttonBoard {
  display: flex;
  flex-wrap: wrap;
  .card {
    margin: 10px;
  }
}

.operation-button-active {
  color: red;
}