#root {
  display: grid;
  grid-template-areas:
    ". . ."
    ". app .";
  grid-template-columns: 10% auto 10%;
  grid-template-rows: 5% auto;
}
.App {
  grid-area: app;
}
.table-preview {
  height: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 40px 30px auto;
  grid-template-areas:
    "header actions"
    "subheader subheader"
    "main main"
}

.table-preview {
  h3.subtitle.is-5 {
    margin: 0;
    align-self: stretch;
    grid-area: subheader;
  }
  h2.title.is-3 {
    grid-area: header;
    margin: 0;
    align-self: stretch;

  }
  .actions {
    grid-area: actions;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 30px;
    grid-template-rows: 1fr;
    .button {
      align-self: start;
    }
  }
  .grid-section {
    grid-area: main
  }
}

.grid-section {
  display: grid;
  grid-template-rows: 1fr 39px;
  grid-template-columns: 1fr;
  grid-template-areas:
    "table"
    "sheet-tabs"
}

.grid-container {
  grid-area: table;
}
.sheet-tabs {
  grid-area: sheet-tabs;
}

.grid-container {
  position: relative;
  height: 100%;
}

.buttonBoard {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
}
